zation);\nconst dialog = container.resolve<IDialog>(IDialog);\n\nconst domains = ref<Array<IDomain>>([]);\nconst tenants = ref<Array<ITenantUser>>([]);\nconst domainTenantRelations = ref<Array<ITenantDomainRelation>>([]);\nconst systemUsers = ref<Array<ISystemUser>>([]);\n\nfunction fillRelationTableData(domains: Array<IDomain>, tenants: Array<ITenantUser>) {\n  const items: Array<ITenantDomainRelation> = [];\n\n  tenants.forEach(tenant => {\n    const relations = tenant.tenantRelations;\n    if (!relations) { return; }\n\n    relations.forEach(relation => {\n      const domain = domains.find(i => i.ID == relation.tenantID)!;\n      const item: ITenantDomainRelation = {\n        domainID: relation.tenantID,\n        domainName: domain.name,\n        tenantID: tenant.ID,\n        tenantName: tenant.name,\n        loginType: relation.loginType,\n      }\n      items.push(item);\n    });\n  });\n\n  domainTenantRelations.value = items;\n}\n\nasync function getData(options: IInitOptions) {\n  await executor.execute(async () => {\n    const domainsProm = options.domains ? systemUserManagement.listDomains() : (async () => domains.value)();\n    const tenantsProm = options.tenants ? systemUserManagement.listTenants() : (async () => tenants.value)();\n    const systemUsersProm = options.systemUsers ? systemUserManagement.listSystemUsers() : (async () => systemUsers.value)();\n\n    [domains.value, tenants.value, systemUsers.value] = await Promise.all([domainsProm, tenantsProm, systemUsersProm]);\n\n    if (options.tenants) {\n      fillRelationTableData(domains.value, tenants.value);\n    }\n  }, { loading: true });\n}\n\nconst checkUserProm = CreatePromiseData<void>();\n\nonBeforeMount(async () => {\n  await executor.execute(async () => {\n    await systemUserManagement.checkUser();\n    checkUserProm.resolver();\n  }, { loading: true });\n});\n\nonMounted(async () => {\n  await checkUserProm.promise;\n  await getData({ domains: true, tenants: true, systemUsers: true });\n});\n\nconst dtBaseOptions = {\n  filterable: false, perPage: 10, perPageValues: [],\n  sortIcon: { base: 'mdi', is: 'mdi mdi-unfold-less-horizontal', up: 'mdi mdi-arrow-up', down: 'mdi mdi-arrow-down' },\n};\n\nconst domainsDtOptions = {\n  ...dtBaseOptions,\n  headings: {\n    ID: \"Domain ID\",\n    name: \"Domain Name\",\n    dbName: \"Database Name\",\n    createdBy: localization.get('applicationListcreatedByHeader'),\n    createDate: localization.get('applicationListcreatedDateHeader'),\n  }\n};\n\nconst tenantsDtOptions = {\n  ...dtBaseOptions,\n  headings: {\n    ID: \"Tenant ID\",\n    name: \" Tenant Name\",\n    createdBy: localization.get('applicationListcreatedByHeader'),\n    createDate: localization.get('applicationListcreatedDateHeader'),\n  }\n};\n\nconst relationsDtOptions = {\n  ...dtBaseOptions,\n  headings: {\n    domainID: \"Domain ID\",\n    domainName: \"Domain Name\",\n    tenantID: \"Tenant ID\",\n    tenantName: \"Tenant Name\",\n    loginType: \"Login Type\",\n  }\n};\n\nconst systemUsersDtOptions = {\n  ...dtBaseOptions,\n  headings: {\n    ID: \"User ID\",\n    userName: \"Domain Name\",\n    loginType: \"Login Type\",\n    createdBy: localization.get('applicationListcreatedByHeader'),\n    createDate: localization.get('applicationListcreatedDateHeader'),\n  }\n};\n\nlet dtOptions: any = { month: '2-digit', day: '2-digit', year: 'numeric', hour: 'numeric', minute: 'numeric', second: 'numeric' };\nconst domainsDatatableColumns = ref(['ID', 'name', 'dbName', 'createdBy', 'createDate', \"actions\"]);\nconst tenantsDatatableColumns = ref(['ID', 'name', 'createdBy', 'createDate', \"actions\"]);\nconst relationsDatatableColumns = ref(['domainID', 'domainName', 'tenantID', 'tenantName', \"loginType\", \"actions\"]);\nconst systemUsersDatatableColumns = ref(['ID', 'userName', \"loginType\", 'createdBy', 'createDate', \"actions\"]);\nconst domainsDataTable = ref(domainsDtOptions);\nconst tenantsDataTable = ref(tenantsDtOptions);\nconst relationsDataTable = ref(relationsDtOptions);\nconst systemUsersDataTable = ref(systemUsersDtOptions);\n\nasync function createNewDomian() {\n  const closeCb = async (reason: \"close\" | \"outclick\" | \"component\") => {\n    if (reason != \"component\") { return; }\n    await getData({ domains: true });\n  };\n\n  dialog.showDialog(defineAsyncComponent(() => import(`../components/dialogs/systemuser/createDomain.vue`)), { closable: true, title: \"Create Domain\", closeCb }, {});\n}\n\nasync function createNewTenant() {\n  const closeCb = async (reason: \"close\" | \"outclick\" | \"component\") => {\n    if (reason != \"component\") { return; }\n    await getData({ tenants: true });\n  };\n\n  dialog.showDialog(defineAsyncComponent(() => import(`../components/dialogs/systemuser/createTenant.vue`)), { closable: true, title: \"Create Tenant\", closeCb }, {});\n}\n\nasync function createNewRelation() {\n  const closeCb = async (reason: \"close\" | \"outclick\" | \"component\") => {\n    if (reason != \"component\") { return; }\n    await getData({ domains: true, tenants: true });\n  };\n\n  const domainOptions = domains.value.map(i => { return { text: i.name, value: i.ID } });\n  const tenantOptions = tenants.value.map(i => { return { text: i.name, value: i.ID } });\n\n  dialog.showDialog(defineAsyncComponent(\n    () => import(`../components/dialogs/systemuser/attachDomainToTenant.vue`)),\n    { closable: true, title: \"Attach Domain To Tenant\", closeCb },\n    { domainOptions, tenantOptions },\n  );\n}\n\nasync function createNewSystemUSer() {\n  const closeCb = async (reason: \"close\" | \"outclick\" | \"component\") => {\n    if (reason != \"component\") { return; }\n    await getData({ systemUsers: true });\n  };\n\n  dialog.showDialog(defineAsyncComponent(() => import(`../components/dialogs/systemuser/createSystemUser.vue`)), { closable: true, title: \"Create New System User\", closeCb }, {});\n}\n\nasync function openUsersDialog(dbName: string) {\n  dialog.showDialog(defineAsyncComponent(\n    () => import(`../components/user/listUsers.vue`)),\n    { closable: true, title: `${dbName}: Users`, width: \"1350px\", maxHeight: \"900px\", },\n    { dbName }\n  );\n}\n\nasync function deleteDomian(domainID: string, domainName: string) {\n  await executor.execute(async () => {\n    await systemUserManagement.deleteDomain(domainID, domainName);\n    await getData({ domains: true, tenants: true });\n  }, { loading: true });\n}\n\nasync function deleteTenant(tenantID: string, tenantName: string) {\n  await executor.execute(async () => {\n    await systemUserManagement.deleteTenant(tenantID, tenantName);\n    await getData({ tenants: true });\n  }, { loading: true });\n}\n\nasync function deleteSystemUser(systemUserID: string, systemUserName: string) {\n  await executor.execute(async () => {\n    await systemUserManagement.deleteSystemUser(systemUserID, systemUserName);\n    await getData({ systemUsers: true });\n  }, { loading: true });\n}\n\nasync function removeRelation(domainID: string, tenantID: string, domainName: string, tenantName: string) {\n  await executor.execute(async () => {\n    await systemUserManagement.detachDomainFromTenant(domainID, tenantID, domainName, tenantName);\n    await getData({ tenants: true });\n  }, { loading: true });\n}\n\n</script>\n<template>\n  <div class=\"content-area\">\n    <div class=\"tables-area\">\n      <div class=\"section-area\">\n        <div>\n          <button @click=\"getData({ domains: true, tenants: true, systemUsers: true })\">Refresh Tables</button>\n        </div>\n      </div>\n      <div class=\"section-area\">\n        <div class=\"header-text\">Domains \n          <button class=\"btn-action\" @click=\"createNewDomian()\">Create New Domain</button></div>\n        <div>\n          <v-client-table ref='table' :data=\"domains\" :columns=\"domainsDatatableColumns\" :options=\"domainsDataTable\">\n            <template #createdDate=\"props\">\n              <span>{{ new Intl.DateTimeFormat(\"tr-TR\", dtOptions).format(new Date(props.row.createDate)) }}</span>\n            </template>\n            <template #actions=\"props\">\n              <div>\n                <button class=\"btn-action\" @click=\"openUsersDialog(props.row.dbName)\">Users</button>\n                <button class=\"btn-action\" @click=\"deleteDomian(props.row.ID, props.row.name)\">Delete</button>\n              </div>\n            </template>\n          </v-client-table>\n        </div>\n      </div>\n      <div class=\"section-area\">\n        <div class=\"header-text\">Tenants <button @click=\"createNewTenant()\">Create New Tenant</button></div>\n        <div>\n          <v-client-table ref='table' :data=\"tenants\" :columns=\"tenantsDatatableColumns\" :options=\"tenantsDataTable\">\n            <template #createdDate=\"props\">\n              <span>{{ new Intl.DateTimeFormat(\"tr-TR\", dtOptions).format(new Date(props.row.createDate)) }}</span>\n            </template>\n            <template #actions=\"props\">\n              <div>\n                <button class=\"btn-action\" @click=\"deleteTenant(props.row.ID, props.row.name)\">Delete</button>\n              </div>\n            </template>\n          </v-client-table>\n        </div>\n      </div>\n      <div class=\"section-area\">\n        <div class=\"header-text\">Tenant & Domain Relations <button @click=\"createNewRelation()\">Add New\n            Relation</button></div>\n        <div>\n          <v-client-table ref='table' :data=\"domainTenantRelations\" :columns=\"relationsDatatableColumns\"\n            :options=\"relationsDataTable\">\n            <template #actions=\"props\">\n              <div>\n                <button\n                  @click=\"removeRelation(props.row.domainID, props.row.tenantID, props.row.domainName, props.row.tenantName)\">Remove</button>\n              </div>\n            </template>\n          </v-client-table>\n        </div>\n      </div>\n      <div class=\"section-area\">\n        <div class=\"header-text\">System Users <button @click=\"createNewSystemUSer()\">Create System User</button>\n        </div>\n        <div>\n          <v-client-table ref='table' :data=\"systemUsers\" :columns=\"systemUsersDatatableColumns\"\n            :options=\"systemUsersDataTable\">\n            <template #actions=\"props\">\n              <div>\n                <button class=\"btn-action\" @click=\"deleteSystemUser(props.row.ID, props.row.userName)\">Delete</button>\n              </div>\n            </template>\n          </v-client-table>\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<style scoped>\n.content-area {\n  overflow-y: scroll;\n  height: 100%;\n}\n\n.tables-area {\n  width: 70%;\n  margin-left: 15%;\n  margin-top: 10px;\n}\n\n.section-area {\n  margin-bottom: 25px;\n}\n\n.header-text {\n  font-weight: bold;\n  margin-bottom: 5px;\n}\n\n.btn-action {\n  margin-left: 10px;\n}\n</style>"],"sourceRoot":""}]);
